import React, { useState, useEffect } from 'react';
import { auth, storage, firestore } from '../firebase';
import { doc, getDoc, updateDoc, deleteField, collection, query, where, getDocs, setDoc } from 'firebase/firestore';
import { updateProfile } from 'firebase/auth';
import { ref, uploadBytesResumable, getDownloadURL, deleteObject } from 'firebase/storage';
import { useParams } from 'react-router-dom';

function ExpertProfile() {
const { customURL } = useParams();
const [user, setUser] = useState(null);
const [firstName, setFirstName] = useState('');
const [lastName, setLastName] = useState('');
const [photoURL, setPhotoURL] = useState(null);
const [previousPhotoRef, setPreviousPhotoRef] = useState(null);
const [message, setMessage] = useState({ text: '', type: '' });
const [updateMessage, setUpdateMessage] = useState({ text: '', type: '' });
const [imageLoadError, setImageLoadError] = useState(false);
const [address, setAddress] = useState('');
const [city, setCity] = useState('');
const [county, setCounty] = useState('');
const [postcode, setPostcode] = useState('');
const [mobileNumber, setMobileNumber] = useState('');
const [role, setRole] = useState('');
const [email, setEmail] = useState(''); 
const [description, setDescription] = useState('');
const [currentKeyword, setCurrentKeyword] = useState('');
const [keywords, setKeywords] = useState([]);

const [currentKeywords, setCurrentKeywords] = useState([]);
const [showUpdateMessage, setShowUpdateMessage] = useState(false);
const [removedKeywordIndices, setRemovedKeywordIndices] = useState([]);



useEffect(() => {
const unsubscribe = auth.onAuthStateChanged(async (authUser) => {
if (authUser) {
setUser(authUser);

const userDocRef = doc(firestore, 'expertApplications', authUser.uid);
const userDocSnapshot = await getDoc(userDocRef);
if (userDocSnapshot.exists()) {
const userData = userDocSnapshot.data();
if (userData.photoURL) {
setPhotoURL(userData.photoURL);
} else {
setPhotoURL(authUser.photoURL);
}
setFirstName(userData.first_name || authUser.displayName.split(' ')[0] || '');
setLastName(userData.last_name || authUser.displayName.split(' ')[1] || '');
setAddress(userData.address || '');
setCity(userData.city || '');
setCounty(userData.county || '');
setPostcode(userData.postcode || '');
setMobileNumber(userData.mobileNumber || '');
setRole(userData.occupation || '');
setEmail(authUser.email); // Set email from authUser
} else {
setPhotoURL(authUser.photoURL);
}
} else {
setAddress('');
setCity('');
setCounty('');
setPostcode('');
setMobileNumber('');
setPhotoURL(null);
setEmail(''); // Clear email
setRole('');
}
});

return () => {
unsubscribe();
};
}, []);

const MAX_FILE_SIZE = 5 * 1024 * 1024;

const handleFileUpload = async (event) => {
    const file = event.target.files[0];
    if (file) {
      if (file.size > MAX_FILE_SIZE) {
        setMessage({ text: 'File size is too big, max 500kb. Please select a smaller file.', type: 'error' });
        setTimeout(() => setMessage({ text: '', type: '' }), 3000); // Clear the message after 3 seconds
        return;
      }
  
      const metadata = {
        contentType: file.type,
      };
  
      if (previousPhotoRef) {
        try {
          await deleteObject(previousPhotoRef);
          setMessage({ text: 'Profile photo removed', type: 'progress' });
          console.log('Profile photo removed');
        } catch (error) {
          setMessage({ text: `Error deleting previous photo: ${error.message}`, type: 'error' });
          console.error('Error deleting previous photo:', error);
        }
      }
  
      const storageRef = ref(storage, `profileImages/${file.name}`);
      const uploadTask = uploadBytesResumable(storageRef, file, metadata);
  
      uploadTask.on(
        'state_changed',
        (snapshot) => {
          const progress = (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
          setMessage({ text: `Upload is ${progress.toFixed(2)}% done`, type: 'progress' });
        },
        (error) => {
          setMessage({ text: `Error uploading image: ${error.message}`, type: 'error' });
          console.error('Error uploading image:', error);
          setImageLoadError(true); // Handle image load error
          // Clear the error message after 3 seconds
          setTimeout(() => setMessage({ text: '', type: '' }), 3000);
        },
        () => {
          getDownloadURL(uploadTask.snapshot.ref).then(async (downloadURL) => {
            console.log('File available at', downloadURL);
            updateProfile(user, { photoURL: downloadURL })
              .then(async () => {
                setMessage({ text: 'Profile photo updated successfully', type: 'success' });
                console.log('Profile photo updated successfully');
                setPhotoURL(downloadURL);
                setPreviousPhotoRef(uploadTask.snapshot.ref);
                setImageLoadError(false); // Reset imageLoadError to false on successful upload
                // Update Firestore document with new photoURL
                const userDocRef = doc(firestore, 'expertApplications', user.uid);
                await updateDoc(userDocRef, { photoURL: downloadURL });
                // Clear the message after 3 seconds
                setTimeout(() => setMessage({ text: '', type: '' }), 3000);
              })
              .catch((error) => {
                setMessage({ text: `Error updating profile photo: ${error.message}`, type: 'error' });
                console.error('Error updating profile photo:', error);
                // Clear the error message after 3 seconds
                setTimeout(() => setMessage({ text: '', type: '' }), 3000);
              });
          });
        }
      );
    }
  };
  
const handleRemoveImage = async () => {
if (!previousPhotoRef) {
setMessage({ text: "No photo to remove", type: "error" });
console.log('No previous photo to delete.');
return;
}
try {
await deleteObject(previousPhotoRef);
setMessage({ text: 'Profile photo removed', type: 'progress' });
console.log('Previous photo deleted successfully');
setPhotoURL(`${process.env.PUBLIC_URL}/images/arts/user.webp`); // Set photoURL to the 404 image path
// The updated photoURL will be logged by the useEffect hook.
setPreviousPhotoRef(null); // Clear the previousPhotoRef state
// Remove the photoURL field from Firestore
const userDocRef = doc(firestore, 'expertApplications', user.uid);
await updateDoc(userDocRef, { photoURL: deleteField() });
} catch (error) {
setMessage({ text: `Error deleting previous photo: ${error.message}`, type: 'error' });
console.error('Error deleting previous photo:', error);
}
};


const clearUpdateMessage = () => {
    setUpdateMessage({ text: '', type: '' });
  };

const updateProfileInfo = async () => {
    if (!user) {
      return;
    }
  
    try {
      const userDocRef = doc(firestore, 'expertApplications', user.uid);
      const userDocSnapshot = await getDoc(userDocRef);
  
      if (userDocSnapshot.exists()) {
        const userData = userDocSnapshot.data();
        const approvedStatus = userData.approved || false; // Preserving the approved status
  
        const updatedUser = auth.currentUser;
        const newPhotoURL = updatedUser.photoURL;
  
        // Update Firestore document with new user data
        await setDoc(userDocRef, {
          displayName: `${firstName} ${lastName}`,
          email: user.email,
          mobileNumber: document.getElementById('inputMobileNumber').value,
          role: document.getElementById('inputRole').value,
          address: document.getElementById('inputAddress').value,
          city: document.getElementById('inputCity').value,
          county: document.getElementById('inputCounty').value,
          postcode: document.getElementById('inputPostcode').value,
          description: document.getElementById('inputDescription').value,
          photoURL: newPhotoURL,
          approved: approvedStatus, // Preserve the approved field
          keywords: currentKeywords,
        });
  
        // Clear the previousPhotoRef state
        setPreviousPhotoRef(null);
  
 
       setUpdateMessage({ text: 'Profile updated successfully', type: 'success' });

       // Clear the message after 3 seconds
       setTimeout(() => clearUpdateMessage(), 3000);
     }
   } catch (error) {
     // Set the error message
     setUpdateMessage({ text: `Error updating profile: ${error.message}`, type: 'error' });
 
     // Clear the error message after 3 seconds
     setTimeout(() => clearUpdateMessage(), 3000);
   }
 };

useEffect(() => {
    const unsubscribe = auth.onAuthStateChanged(async (authUser) => {
      if (authUser) {
        setUser(authUser);
  
        // Assuming 'expertApplications' is the collection containing user data
        const userDocRef = doc(firestore, 'expertApplications', authUser.uid);
        const userDocSnapshot = await getDoc(userDocRef);
  
        if (userDocSnapshot.exists()) {
          const userData = userDocSnapshot.data();
          setKeywords(userData.keywords || []);
        }
      }
    });
  
    return () => {
      unsubscribe();
    };
  }, []);
  

    const handleAddKeyword = () => {
    if (currentKeyword.trim() !== '') {
    if (currentKeywords.length < 5) {
    const updatedKeywords = [...currentKeywords, currentKeyword.trim()];
    setCurrentKeywords(updatedKeywords);
    setCurrentKeyword('');
    }
    }
    };
    const handleRemoveKeyword = (index) => {
    const updatedKeywords = [...currentKeywords];
    updatedKeywords.splice(index, 1);
    setCurrentKeywords(updatedKeywords);
    setRemovedKeywordIndices([...removedKeywordIndices, index]);
    };

    const MAX_KEYWORDS_LIMIT = 5; 
    

return (
    <div className="profile-container">
    <div className="profile-intro-section">
    <div className="container">
    <div className="row align-items-center text-white">
    {user && (
    <div className="welcome-text">
    <h1 className="display-3 fw-bold text-capitalize">
    Welcome, <span className="email-h3">{user.email}</span>!
    </h1>
    </div>
    )}
    </div>
    </div>
    </div>
    <div className="profile-section">
    <div className="content profile-content ">
    <div className="row">
    <div className="my-4">
    <h3>My Profile</h3>
    <hr />
    </div>
    <form className="file-upload">
    
    <div className="row mb-4 gx-4">
    <div className="col-xl-8 col-12">
    <div className="bg-white shadow-sm mb-4 card border-light">
    <div className="card-body card-profile">
    <div className="row g-3">
    <h4 className="mb-4">General information</h4>
    <div className="col-md-6">
    <label className="form-label">First Name *</label>
    <input
    type="text"
    className="form-control form-control-lg"
    placeholder=""
    aria-label="First name"
    value={firstName} // Display user's first name here
    onChange={(e) => setFirstName(e.target.value)}
    readOnly // Make it read-only
    style={{ pointerEvents: 'none' }}
    />
    </div>
    <div className="col-md-6">
    <label className="form-label">Last Name *</label>
    <input
    type="text"
    className="form-control form-control-lg"
    placeholder=""
    aria-label="Last name"
    value={lastName} // Display user's last name here
    onChange={(e) => setLastName(e.target.value)}
    readOnly // Make it read-only
    style={{ pointerEvents: 'none' }}
    />
    </div>
    <div className="col-md-6">
    <label htmlFor="inputEmail4" className="form-label">Email *</label>
    <input
    type="email"
    className="form-control form-control-lg"
    id="inputEmail4"
    value={user ? user.email : ''} // Display user's email here
    readOnly // Make it read-only
    style={{ pointerEvents: 'none' }}
    />
    </div>
    <div className="col-md-6">
    <label className="form-label">Mobile number *</label>
    <input
    id="inputMobileNumber"
    type="text"
    className="form-control form-control-lg"
    placeholder="Enter your Phone number"
    aria-label="Phone number"
    value={mobileNumber} // Display user's mobile number here
    onChange={(e) => setMobileNumber(e.target.value)}
    />
    </div>
    <div className="col-md-12">
    <label className="form-label">Role/Expertise *</label>
    <input
    id="inputRole"
    type="text"
    className="form-control form-control-lg"
    placeholder="Enter your role/expertise"
    aria-label="your Role"
    value={role} // Display user's mobile number here
    onChange={(e) => setRole(e.target.value)}
    readOnly // Make it read-only
    style={{ pointerEvents: 'none' }}
    />
    </div>
    <div className="col-md-12">
    <label className="form-label">Description of Expertise</label>
    <textarea
    id="inputDescription"
    type="text"
    className="form-control"
    rows="4"
    placeholder="Enter a brief description of your expertise or how you can help"
    value={description} // Create a 'description' state and update it accordingly
    onChange={(e) => setDescription(e.target.value)}
    style={{ resize: 'none' }}
    />
    </div>
    </div>
    <div className="py-5 rounded mt-4">
    <div className="row g-3">
    <h4 className="mb-4 mt-0">Address</h4>
    <div className="col-md-6">
    <label className="form-label">Address</label>
    <input
    id="inputAddress"
    type="text"
    className="form-control form-control-lg"
    placeholder="Enter your home address"
    value={address} // Display user's address here
    onChange={(e) => setAddress(e.target.value)}
    />
    </div>
    <div className="col-md-6">
    <label className="form-label">City</label>
    <input
    id="inputCity"
    type="text"
    className="form-control form-control-lg"
    placeholder="Enter City"
    value={city} // Display user's city here
    onChange={(e) => setCity(e.target.value)}
    />
    </div>
    <div className="col-md-6">
    <label className="form-label">County</label>
    <input
    id="inputCounty"
    type="text"
    className="form-control form-control-lg"
    placeholder="County"
    value={county} // Display user's county here
    onChange={(e) => setCounty(e.target.value)}
    />
    </div>
    <div className="col-md-6">
    <label className="form-label">Postcode</label>
    <input
    id="inputPostcode"
    type="text"
    className="form-control form-control-lg"
    placeholder="Postcode"
    value={postcode} // Display user's postcode here
    onChange={(e) => setPostcode(e.target.value)}
    />
    </div>
    
    </div>
    </div>
    <div className='mt-3'>
    <button
    type="button"
    className="btn btn-primary btn-lg mb-4"
    onClick={updateProfileInfo}
    
    >
    Update profile
    </button>
    <div>
  {updateMessage.type === 'error' && (
    <p className="fw-light" style={{ color: 'red' }}>{updateMessage.text}</p>
  )}
  {updateMessage.type === 'success' && (
    <p className="fw-light" style={{ color: 'green' }}>{updateMessage.text}</p>
  )}
</div>
    
    </div>
    </div>
    </div>
    </div>
    
    <div className="col-xl-4 col-12">
    <div className="bg-white shadow-sm mb-4 card border-light">
    <div className="card-profile">
    <div className="profile-cover rounded-top">
    </div>
    <div className="row g-3 ">
    <div className="text-center">
    <div className="pb-5 card-body">
    {/* Center the image using Bootstrap's flexbox classes */}
    {!imageLoadError && photoURL && (
    <img
    src={photoURL}
    alt="User Profile"
    className="card-img user-avatar large-avatar rounded-circle mx-auto mt-n7 mb-5"
    onError={() => setImageLoadError(true)}
    onChange={(e) => setPhotoURL(e.target.value)}
    />
    )}
    
    {imageLoadError && (
    <img
    src={`${process.env.PUBLIC_URL}/images/arts/user.webp`}
    alt="Custom 404 Image"
    className="card-img user-avatar large-avatar rounded-circle mx-auto mt-n7 mb-5"
    onChange={(e) => setPhotoURL(e.target.value)}
    />
    )}
    
    <div>
    {message.type === 'error' && (
    <p className="fw-light" style={{ color: 'red' }}>{message.text}</p>
    )}
    {message.type === 'success' && (
    <p className="fw-light" style={{ color: 'green' }}>{message.text}</p>
    )}
    {message.type === 'progress' && (
    <p className="fw-light" style={{ color: 'gray' }}>{message.text}</p>
    )}
    </div>
    <div className="card-title h5">{`${firstName} ${lastName}`} </div>
    <div className="fw-normal card-subtitle mb-4 h6">{role}</div>
    <button type="button" className="me-4 btn btn-primary btn-sm rounded-pill pt-2 pb-2">
    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="currentColor" width="25" className="me-2 svg-inline--fa ">
    <path d="M6.25 6.375a4.125 4.125 0 118.25 0 4.125 4.125 0 01-8.25 0zM3.25 19.125a7.125 7.125 0 0114.25 0v.003l-.001.119a.75.75 0 01-.363.63 13.067 13.067 0 01-6.761 1.873c-2.472 0-4.786-.684-6.76-1.873a.75.75 0 01-.364-.63l-.001-.122zM19.75 7.5a.75.75 0 00-1.5 0v2.25H16a.75.75 0 000 1.5h2.25v2.25a.75.75 0 001.5 0v-2.25H22a.75.75 0 000-1.5h-2.25V7.5z" />
    </svg>
    Message
    </button>
    <button type="button" className="btn btn-secondary btn-sm rounded-pill pt-2 pb-2">
    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="currentColor" width="25" className="me-2 svg-inline--fa ">
    <path fillRule="evenodd" d="M4.804 21.644A6.707 6.707 0 006 21.75a6.721 6.721 0 003.583-1.029c.774.182 1.584.279 2.417.279 5.322 0 9.75-3.97 9.75-9 0-5.03-4.428-9-9.75-9s-9.75 3.97-9.75 9c0 2.409 1.025 4.587 2.674 6.192.232.226.277.428.254.543a3.73 3.73 0 01-.814 1.686.75.75 0 00.44 1.223zM8.25 10.875a1.125 1.125 0 100 2.25 1.125 1.125 0 000-2.25zM10.875 12a1.125 1.125 0 112.25 0 1.125 1.125 0 01-2.25 0zm4.875-1.125a1.125 1.125 0 100 2.25 1.125 1.125 0 000-2.25z" clipRule="evenodd" />
    </svg>
    Connect
    </button>
    
    </div>
    </div>
    </div>
    </div>
    </div>
    
    <div className="bg-white shadow-sm mb-4 card border-light">
    <div className="card-body card-profile">
    <div className="row g-3">
    <h5 className="mb-4">Select profile photo</h5>
    <div className="d-xl-flex align-items-center">
    <div className="file-field">
    <div className="d-flex justify-content-xl-center">
    <div className="d-flex">
    <span
    className="icon icon-md"
    onClick={() => {
    document.getElementById('customFile').click();
    }}
    style={{ cursor: 'pointer' }}
    >
    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="45" className="w-6 h-6 me-3">
    <path fillRule="evenodd" d="M18.97 3.659a2.25 2.25 0 00-3.182 0l-10.94 10.94a3.75 3.75 0 105.304 5.303l7.693-7.693a.75.75 0 011.06 1.06l-7.693 7.693a5.25 5.25 0 11-7.424-7.424l10.939-10.94a3.75 3.75 0 115.303 5.304L9.097 18.835l-.008.008-.007.007-.002.002-.003.002A2.25 2.25 0 015.91 15.66l7.81-7.81a.75.75 0 011.061 1.06l-7.81 7.81a.75.75 0 001.054 1.068L18.97 6.84a2.25 2.25 0 000-3.182z" clipRule="evenodd" />
    </svg>
    </span>
    <input
    type="file"
    accept="image/*"
    id="customFile"
    onChange={handleFileUpload}
    style={{ display: 'none' }}
    />
    <div className="d-md-block text-start">
    <div className="fw-normal text-dark mb-1">Choose Image</div>
    <div className="text-gray small">JPG, GIF or PNG. Max size of 800K</div>
    </div>
    </div>
    </div>
    </div>
    </div>
    </div>
    </div>
    </div>
    <div className="bg-white shadow-sm mb-4 card border-light">
    <div className="card-body card-profile">
    <div className="row g-3">
    <h5 className="mb-2">Choose 5 Keywords</h5>
    <p>we recommend choosing keywords that you think will get you highest in the search results</p>
  
 
    <div className="me-3">
    <input
    type="text"
    className='form-control form-control-lg'
    placeholder="Type a keyword and press Space to add"
    value={currentKeyword}
    onChange={(e) => setCurrentKeyword(e.target.value)}
    onKeyDown={(e) => {
    if (e.key === ' ' && currentKeyword.trim() !== '') {
    e.preventDefault(); // Prevent the Space character from being entered into the input
    handleAddKeyword(); // Call the handleAddKeyword function
    }
    }}
    disabled={keywords.length >= 5}
    />
    

    </div>
   
  
    <div className="keywords-list">
  {currentKeywords.map((keyword, index) => (
    <div key={index} onClick={() => handleRemoveKeyword(index)} className="btn btn-primary rounded-pill d-inline-flex align-items-center me-2 mb-2">
      {keyword}
      <svg onClick={() => handleRemoveKeyword(index)} xmlns="http://www.w3.org/2000/svg" color='#FFFFFF' fill="none" viewBox="0 0 24 24" strokeWidth={2} stroke="currentColor" height="25" width="25" style={{ marginLeft: 'auto' }}>
        <path strokeLinecap="round" strokeLinejoin="round" d="M6 18L18 6M6 6l12 12" />
      </svg>
    </div>
  ))}

  {keywords.map((keyword, index) => (
    <div key={index} onClick={() => removeKeyword(index)} className="btn btn-primary rounded-pill d-inline-flex align-items-center me-2 mb-2">
      {keyword}
      <svg onClick={() => removeKeyword(index)} xmlns="http://www.w3.org/2000/svg" color='#FFFFFF' fill="none" viewBox="0 0 24 24" strokeWidth={2} stroke="currentColor" height="25" width="25" style={{ marginLeft: 'auto' }}>
        <path strokeLinecap="round" strokeLinejoin="round" d="M6 18L18 6M6 6l12 12" />
      </svg>
    </div>
  ))}

{currentKeywords.length + keywords.length >= MAX_KEYWORDS_LIMIT && (
  <p className="error-message">Keyword limit reached. You cannot add more keywords.</p>
)}
</div>

    </div>
    </div>
    </div>
    </div>
    </div>
    
    <div className="gap-3 d-md-flex justify-content-md-end text-center">
    <button type="button" className="btn btn-danger btn-lg">
    Delete profile
    </button>
    </div>
    </form>
    </div>
    </div>
    </div>
    </div>
    );
    }
    

export default ExpertProfile;

